import axios from 'axios'
export default {
  name: 'projectEditForm',
  data () {
    return {
      options: [{
        value: 1,
        label: '课题等待审核'
      }, {
        value: 2,
        label: '项目等待审核'
      }, {
        value: 3,
        label: '运行中'
      }, {
        value: 4,
        label: '已完成'
      }],
      project: '',
      edit: false,
      addTask: false,
      deleteTask: false,
      editTask: false,
      taskID: '',
      taskName: '',
      name: '',
      startTime: '',
      endTime: '',
      content: '',
      target: '',
      managerName: '',
      teacherList: [],
      editTaskForm: {
        name: '',
        projectID: parseInt(sessionStorage.getItem('pid')),
        startTime: '',
        endTime: '',
        leaderID: '',
        content: '',
        target: '',
        state: ''
      },
      task: {
        name: '',
        projectID: parseInt(sessionStorage.getItem('pid')),
        startTime: '',
        endTime: '',
        leaderID: '',
        content: '',
        target: ''
      },
      taskList: []
    }
  },
  methods: {
    // getPIList () {
    //   axios.get(this.url + '/user/level/' + this.PI)
    //     .then(this.getTeacherListSucc)
    // },
    getPRList () {
      axios.get(this.url + '/user/level/' + this.PR)
        .then(this.getTeacherListSucc)
    },
    getTeacherListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.teacherList = res
      } else {
        alert('获取老师列表失败，请稍后再试！')
      }
    },
    delTask () {
      this.$axios({
        method: 'delete',
        url: this.url + '/module/id/' + this.taskID
      }).then((res) => {
        if (res.status === 200) {
          alert('删除成功！')
          axios.get(this.url + '/module/project' + sessionStorage.getItem('pid'))
            .then(this.getTaskListSucc)
          this.deleteTask = false
          this.getTaskList()
        }
      }).catch((err) => {
        alert(err)
      })
    },
    handleEdit (taskID) {
      this.editTask = true
      this.getPRList()
      axios.get(this.url + '/module/id/' + taskID)
        .then(this.getTaskSucc)
    },
    getTaskSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.editTaskForm = res
        // this.editTaskForm.name = res.name
        // this.editTaskForm.startTime = res.startTime
        // this.editTaskForm.endTime = res.endTime
        // this.editTaskForm.content = res.content
        // this.editTaskForm.target = res.target
        // this.editTaskForm.leaderName = res.leaderName
        // this.editTaskForm.leaderID = res.leaderID
        // if (res.state === this.TaskWaitForCheck) this.editTaskForm.stateText = '课题等待审核'
        // else if (res.state === this.ProjectWaitForCheck) this.editTaskForm.stateText = '项目等待审核'
        // else if (res.state === this.Run) this.editTaskForm.stateText = '运行中'
        // else if (res.state === this.Finished) this.editTaskForm.stateText = '已完成'
        this.editTaskForm.state = res.state
        this.editTask = true
      } else alert(res.data)
    },
    handleDelete (taskID, taskName) {
      this.deleteTask = true
      this.taskName = taskName
      this.taskID = taskID
    },
    handleAddTask () {
      // this.getPIList()
      this.getPRList()
      this.task.name = ''
      this.task.startTime = ''
      this.task.endTime = ''
      this.task.content = ''
      this.task.target = ''
      this.task.leaderID = -1
      this.addTask = true
    },
    getProject () {
      axios.get(this.url + '/project/id/' + sessionStorage.getItem('pid'))
        .then(this.getProjectSucc)
    },
    getProjectSucc (res) {
      res = res.data
      if (res) {
        this.project = res
        this.name = res.name
        this.startTime = res.startTime
        this.endTime = res.endTime
        this.content = res.content
        this.target = res.target
        this.managerName = res.managerName
      }
    },
    getTaskList () {
      axios.get(this.url + '/module/project/' + sessionStorage.getItem('pid'))
        .then(this.getTaskListSucc)
    },
    getTaskListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.taskList = res
      } else alert(res.date)
    },
    updateTask () {
      if (this.editTaskForm.leaderID === -1) {
        this.$notify.warning({
          title: '请选择课题负责人！'
        })
        return
      }
      if (this.editTaskForm.name === '') {
        this.$notify.warning({
          title: '请输入课题名称！'
        })
        return
      }
      this.$axios({
        method: 'put',
        url: this.url + '/module/',
        data: this.editTaskForm
      }).then((res) => {
        if (res.status === 200) {
          // res = res.data
          alert('提交成功！')
        } else alert(res.data)
      }).catch((err) => {
        alert(err)
      })
      this.editTask = false
    },
    submitTask () {
      if (this.task.leaderID === -1) {
        this.$notify.warning({
          title: '请选择课题负责人！'
        })
        return
      }
      if (this.task.name === '') {
        this.$notify.warning({
          title: '请输入课题名称！'
        })
        return
      }
      this.$axios({
        method: 'post',
        url: this.url + '/module/',
        data: this.task
      }).then((res) => {
        if (res.status === 200) {
          res = res.data
          alert('提交成功！')
          this.taskList.push({
            name: this.task.name,
            id: res.id
          })
        } else alert(res.data)
      }).catch((err) => {
        alert(err)
      })
      this.addTask = false
    },
    submit () {
      this.$axios({
        method: 'put',
        url: this.url + '/project/',
        data: {
          id: parseInt(sessionStorage.getItem('pid')),
          // state: this.ProjectWaitForCheck,
          managerID: this.project.managerID,
          name: this.name,
          startTime: this.startTime,
          endTime: this.endTime,
          content: this.content,
          target: this.target
        }
      }).then((res) => {
        if (res.status === 200) alert('提交成功！')
        else alert(res.data)
        this.$router.push('/project')
      }).catch((err) => {
        alert(err)
      })
      this.edit = false
    }
  },
  mounted () {
    this.getProject()
    this.getTaskList()
  }
}



